import { NgModule } from '@angular/core';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { CommonModule } from '@angular/common';

import { UserscheduleComponent } from './userschedule.component';
import { UserupcomingComponent } from './userupcoming/userupcoming.component';
import { UserUnjoinConfirmPopup } from './userupcoming/userunjoinPopup/userUnjoinPopup.component';
import { UserwishlistComponent } from './userwishlist/userwishlist.component';
import { routing } from './userschedule.routing';
import { CoreModule } from '../../core/core.module';
import { ModalModule } from 'angular2-modal';
import { BootstrapModalModule } from 'angular2-modal/plugins/bootstrap';
import { UserRemoveWhishlistPopup } from "user/userschedule/userupcoming/removeWhishlistPopup/removeWhishlistPopup.component";
import { ReactiveFormsModule } from '@angular/forms';
import { CoreModuleNew } from 'newcore/new-core.module';
import { JoinClassPopupModule } from 'joinclasspopup/joinclasspopup.module';
@NgModule({
  imports: [
    routing,
    InfiniteScrollModule,
    CoreModule,
    CommonModule,
    ModalModule.forRoot(),
    BootstrapModalModule,
    ReactiveFormsModule,
    CoreModuleNew,
    JoinClassPopupModule
  ],
  declarations: [
    UserscheduleComponent,
    UserupcomingComponent,
    UserUnjoinConfirmPopup,
    UserwishlistComponent,
    UserRemoveWhishlistPopup,
  ],
  entryComponents: [
    UserupcomingComponent,
    UserUnjoinConfirmPopup,
    UserwishlistComponent,
    UserRemoveWhishlistPopup
  ]
})
export class UserScheduleModule { }



