import { MixpanelService } from './../../../../core/mixpanelService.service';
import { ClassDetail } from './../../../../core/models/classListResponse';
import { GlobalSubscriptionService } from './../../../../app/globalSubcription.service';
import { Component, OnInit } from '@angular/core';
import { DialogRef, ModalComponent, CloseGuard } from 'angular2-modal';
import { Modal,BSModalContext} from 'angular2-modal/plugins/bootstrap';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { HttpUserAuthUtilsService } from "../../../../core/http_wrapper/http-userauth-utils.service";
import { Constants, MixpanelEvent } from '../../../../core/constants';

declare var $: any;

export class AdditionPopupWindowData extends BSModalContext {
   public message:string;
   public title:string;
  public whishlist:ClassDetail;
   



}

@Component({
  selector: 'app-userRemoveWhishList',
  templateUrl: './removeWhishlistPopup.component.html',
  styleUrls: ['./removeWhishlistPopup.component.css']
})

export class UserRemoveWhishlistPopup implements CloseGuard, ModalComponent<AdditionPopupWindowData>,OnInit {
  
  context: AdditionPopupWindowData;
  showLoader:boolean=false;
  message:string;
  title:string; 
  whishlist:ClassDetail;

  beforeDismiss(): boolean | Promise<boolean> {
    throw new Error("Method not implemented.");
  }
  beforeClose(): boolean | Promise<boolean> {
    throw new Error("Method not implemented.");
  }
    
  constructor(public modal: Modal,public dialog: DialogRef<AdditionPopupWindowData>,private http_user: HttpUserAuthUtilsService,private globalSubscriptionService:GlobalSubscriptionService, private mixpanel: MixpanelService) {
  this.context = dialog.context; 
  this.message=this.context.message;
  this.title=this.context.title;
  this.whishlist=this.context.whishlist;
  


   }

  ngOnInit() {
  }


  ngAfterViewInit(){
       $(".modal-dialog ").css("max-width", "400px");
    }

  closePopup(){
      this.dialog.close();
  }
    removeWhishlist(){
    this.http_user.removeFromWishList(this.whishlist.wishListId,this,true);    
  }
  onSuccess(type: any, responsedata: any) {
    if (type === Constants.removeFromWishListApi) {
        this.closePopup()
        this.globalSubscriptionService.wishlistrefreshclass('wishlist');
        this.mixpanel.logEventWithUnfavourite(MixpanelEvent.removeFromWishlist, this.whishlist);


    }
  }
  onFailure(type: any, response: string) {
     
  }   
}



