import { JoinclasspopupComponent } from './../../../joinclasspopup/joinclasspopup.component';
import { GlobalSubscriptionService } from './../../../app/globalSubcription.service';
import { ClassDetail } from 'core/models/classListResponse';
import { unJoinTrainerClass } from '../../../core/http_wrapper/requestModels/classUnjoinRequest';
import { element } from 'protractor';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { LocalStorageService } from '../../../core/service/local-storage.service';
import { HttpSuccesFailureResponse } from '../../../core/http_wrapper/http_wrapper_response';
import { HttpUserAuthUtilsService } from '../../../core/http_wrapper/http-userauth-utils.service';
import { HttpUserUtilsService } from '../../../core/http_wrapper/http-user-utils.service';
import { UserSession } from '../../../core/sessions';
import { Constants, InfiniteScroll } from '../../../core/constants';
import { UserUpcomingClassDetail } from '../../../core/models/userUpcomingClassesResponse';
import { Subscription } from 'rxjs';
import { ConfirmpopupComponent } from '../../../common/confirmpopup/confirmpopup.component';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { UserUnjoinConfirmPopup } from './userunjoinPopup/userUnjoinPopup.component';
import { Router } from '@angular/router';


@Component({
  selector: 'app-userupcoming',
  templateUrl: './userupcoming.component.html',
  styleUrls: ['./userupcoming.component.css']
})
export class UserupcomingComponent implements OnInit, HttpSuccesFailureResponse {
  usercomingclasses: ClassDetail[] = [];
  upcommingClassCount = 0;
  classTimingId: number;
  classDate: string;
  currentPage = 0;
  isLoadMoreData = true;
  lastScrollDistance = 0;
  listIsLaoding = false;
  isLoaded = false;
  session: any;
  public throttle = InfiniteScroll.throttle;
  public scrollDistance = InfiniteScroll.scrollDistance;
  public scrollUpDistance = InfiniteScroll.scrollUpDistance;
  channelName:any;
  userAgent:any;
  otherAppLink:any = '';
  platformNameString:any;
  browserName:any;
  currency:any;

  private subscription: Subscription;
   @Output() updateupcommingclasscount = new EventEmitter();

  constructor(private router: Router,private http_user: HttpUserAuthUtilsService, private globalSubscriptionService: GlobalSubscriptionService, private modal: Modal) { }

  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();
    this.subscription = this.globalSubscriptionService.userUpcommingClassState
            .subscribe((refresh) => {
              this.upcominglist();
            });
    this.upcominglist();

    this.browserName = Constants.getBrowserName();
    this.userAgent = this.getMobileOperatingSystem();

  }

  // navigateTo(type){
  //   if(type=='findClassPage') { this.router.navigate(['/classes']); }
  // }

  getPlatformNameString(platform){
    return Constants.getPlatformNameStringFunction(platform);
  }

  upcominglist() {
    this.resetBeforeLoad();
    this.listIsLaoding = true;
    this.http_user.userupcomingclasses(LocalStorageService.getUserId(), this.currentPage, this, true)
  }
  openconfirmpopup(message, title, session,isTimeLessAllowedForRefund?) {
      let isTimeLessThanAllowedForRefund = isTimeLessAllowedForRefund;
     return this.modal.open(UserUnjoinConfirmPopup,  overlayConfigFactory({message, title, session,isTimeLessThanAllowedForRefund }, BSModalContext));
}
  unjoinSession(joinClassId,canceletionType) { 
    const sessionId: any = this.usercomingclasses.filter((element) => {
        return element.joinClassId === joinClassId;
      });
    this.session = sessionId[0];
    this.session.Origin = 'UpComing';
    this.session.cancelType = canceletionType;
    if (this.session.priceModel === 'FOC') {
      return this.openconfirmpopup(Constants.unJoinAlert, 'unjoinsession', this.session);
    }
    if (LocalStorageService.getRefundValue()) {
      if (this.session.priceModel === 'PT' || this.session.priceModel === 'WORKSHOP') {
        this.timeCalculation(this.session,  this.session.cancellationTime,
          this.session.cancellationMessage)
        return
       //return this.openconfirmpopup(LocalStorageService.getRefundValue().specialClassCancellationPolicy, 'unjoinsession', this.session)
      }
       if(this.session.priceModel === 'CHARGABLE' && this.session.videoClass == true){
        this.timeCalculation(this.session, this.session.cancellationTime,
        this.session.cancellationMessage)
      }

      if(this.session.priceModel === 'CHARGABLE' && this.session.videoClass == false){
        this.timeCalculation(this.session,  this.session.cancellationTime,
          this.session.cancellationMessage)
      }
      // this.timeCalculation(this.session, +LocalStorageService.getRefundValue().refundAllowedbefore,
      //  LocalStorageService.getRefundValue().cancellationPolicy)
  } else {
    this.http_user.defaultCancellationPolicy(this, true)
  }
  }
  unjoinSessionBookedWithFriend(joinClassId,canceletionType) {
    
    const sessionId: any = this.usercomingclasses.filter((element) => {
        return element.refBookingId === joinClassId;
      });
      if(sessionId&&sessionId[0]){
        this.session = sessionId[0];
        this.session.Origin = 'UpComing';
        this.session.cancelType = canceletionType;

        if (this.session.priceModel === 'FOC') {
          return this.openconfirmpopup(Constants.unJoinAlert, 'unjoinsession', this.session);
        }
        if (LocalStorageService.getRefundValue()) {
          if (this.session.priceModel === 'PT' || this.session.priceModel === 'WORKSHOP') {
            this.timeCalculation(this.session, +LocalStorageService.getRefundValue().refundAllowedbeforeForSpecial,
       LocalStorageService.getRefundValue().specialClassCancellationPolicy)
       return
          }

          if(this.session.priceModel === 'CHARGABLE' && this.session.videoClass == true){
            this.timeCalculation(this.session, +LocalStorageService.getRefundValue().refundAllowedbefore,
           LocalStorageService.getRefundValue().cancellationPolicy)
          }
    
          if(this.session.priceModel === 'CHARGABLE' && this.session.videoClass == false){
            this.timeCalculation(this.session, +LocalStorageService.getRefundValue().refundAllowedbeforeForPhysical,
           LocalStorageService.getRefundValue().physicalCancellationPolicy)
          }

          // this.timeCalculation(this.session, +LocalStorageService.getRefundValue().refundAllowedbefore,
          //  LocalStorageService.getRefundValue().cancellationPolicy)
      } else {
        this.http_user.defaultCancellationPolicy(this, true)
      }
      }else{
       alert("There is some issue in canceling friends booking please try on "+UserSession.getDynamicElements(Constants.GymName)); 
      }
    
  }
  timeCalculation(session: any, refundAllowedbefore: number, cancellationPolicy: any) { 
    const sessionDate = new Date(session.classDate);
    const sessionTime = session.fromTime.split(':');
    sessionDate.setHours(sessionTime[0]);
    sessionDate.setMinutes(sessionTime[1]);
    sessionDate.setSeconds(sessionTime[2]);
    const today = new Date();
    let difference = Math.abs(sessionDate.getTime() - today.getTime());
    difference = (difference / 1000) / 3600;
    if (difference <= refundAllowedbefore) { 
      this.openconfirmpopup(cancellationPolicy, 'NO REFUND', session,true);
    } else {
      if(session.cancelType=='BOTH'){
        this.openconfirmpopup(Constants.unJoinAlertBoth, 'unjoinsession', session);

      }
      else if(session.cancelType=='FRIEND'){
        this.openconfirmpopup(Constants.unJoinAlertFriend, 'unjoinsession', session);

      }else{
        this.openconfirmpopup(Constants.unJoinAlert, 'unjoinsession', session);

      }
    }
  }

  getMobileOperatingSystem() {
    const userAgent = window.navigator.userAgent || window.navigator.vendor;

    // Windows Phone must come first because its UA also contains "Android"
    if (/windows phone/i.test(userAgent)) {
        return 'Windows Phone';
    }

    if (/android/i.test(userAgent)) {
        return 'Android';
    }

    // iOS detection from: http://stackoverflow.com/a/9039885/177710
    if (/iPad|iPhone|iPod/.test(userAgent)) {
        return 'iOS';
    }

    return 'web';
}

  callLiveVideoTokenAPI(classSessionId,link){
    this.channelName = classSessionId;
    //call live video api and in it's success if token is found , redirect to the url

    if(link.length>0){
      window.open(link, "_blank");
    }
    else{
      if(this.browserName=='chrome' || this.browserName=='safari' || this.browserName=='firefox' || this.browserName=='opera'){
        this.userAgent=='web' ? this.http_user.getToken(classSessionId,LocalStorageService.getUserId(),this,true) : alert(Constants.alertVideoBroadcastingUser)
      }
      else{ 
        alert("For seamless experience please download Google Chrome browser. The browser you are using is currently not supported by "+UserSession.getDynamicElements(Constants.GymName));
      }
    }
  }

  
  onSuccess(type: any, responsedata: any) {
    if (type === Constants.CancellationPolicy) {
      LocalStorageService.setRefundPolicy(responsedata);
      if (this.session.priceModel === 'PT' || this.session.priceModel === 'WORKSHOP') {
        return this.openconfirmpopup(responsedata.specialClassCancellationPolicy, 'unjoinsession', this.session)
      }
      if (this.session.priceModel === 'CHARGABLE' && this.session.videoClass == true) {
        this.timeCalculation(this.session, responsedata.refundAllowedbefore, responsedata.cancellationPolicy)
            }
      if (this.session.priceModel === 'CHARGABLE' && this.session.videoClass === false) {
        this.timeCalculation(this.session, responsedata.refundAllowedbeforeForPhysical, responsedata.physicalCancellationPolicy)
            }
    }
    if (type === Constants.UserUpcomingClasses) {
    this.listIsLaoding = false;
    const temp_list: any[] = responsedata.data;
    temp_list.forEach(element => {
     this.usercomingclasses.push(element);
   });

  if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }
      this.upcommingClassCount = this.usercomingclasses.length;
      this.updateupcommingclasscount.next(this.usercomingclasses.length);

    }

    if(type === Constants.getTokenAPI) {
      if (responsedata.data.hasOwnProperty("token")) {
         LocalStorageService.setBroadCastToken(responsedata.data.token);
         LocalStorageService.setChannelName(this.channelName);
        this.router.navigate(['/videobroadcast']);
      }
    }

     if (type === Constants.unjoinTrainerclass) {
    }
    if (type === Constants.unjoinUserClass) {
      this.upcominglist();
    }
    if (type === Constants.trainerFilterListApi) {
      const classdetail = responsedata.data;
      classdetail.userJoinStatus = true;
      return this.modal.open(JoinclasspopupComponent, overlayConfigFactory({ classdetail }, BSModalContext))

    }

  }
  onFailure(type: any, response: string) {
    this.listIsLaoding = false;
    if (type === Constants.CancellationPolicy) {
      this.timeCalculation(this.session, 2, Constants.unJoinRefundAlert);
    }
    if(type === Constants.getTokenAPI) {
      alert(response);
    }
    if (type == Constants.UserUpcomingClasses) {
    }
    if (type == Constants.trainerFilterListApi) {

    }
  }
  onScroll() {

  }
      onScrollDown() {
        if (this.isLoadMoreData &&  !this.listIsLaoding) {
          this.listIsLaoding = true;
          this.currentPage = this.currentPage + 1;
          this.http_user.userupcomingclasses(LocalStorageService.getUserId(), this.currentPage, this, false)
        }
      }
      resetBeforeLoad() {
        this.currentPage = 0;
        this.isLoadMoreData = true;
        this.usercomingclasses = [];

      }
        openClassJoinPopup(data) {
        this.http_user.getClassDetailList(data.classSessionId, data.classDate, this, true);
    }

}



