import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Constants } from "../../core/constants";
import { LocalStorageService } from "../../core/service/local-storage.service";
import { UserUpcomingClass } from "../../core/models/userUpcomingClassesResponse";
import { ActivatedRoute } from "@angular/router";
import { HttpUserAuthUtilsService } from '../../core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from '../../core/http_wrapper/http_wrapper_response';
import { ClassDetail } from '../../core/models/classListResponse';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { JoinclasspopupComponent } from '../../joinclasspopup/joinclasspopup.component';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { ModelpopupLrComponent } from '../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { UserSession } from '../../core/sessions';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-userschedule',
  templateUrl: './userschedule.component.html',
  styleUrls: ['./userschedule.component.css']
})
export class UserscheduleComponent implements OnInit, HttpSuccesFailureResponse {
  upcommingclassnumber: any = "0";
  whishlistclassnumber: any = "0";

  constructor(private _activatedRoute: ActivatedRoute,
    private _httpUserAuth: HttpUserAuthUtilsService,
    private modal: Modal,
    private titleService:Title
  ) { }

  ngOnInit() {
    this.titleService.setTitle(UserSession.getSeoElements.title)

    this._activatedRoute.params.subscribe(params => {
      if (params["sessionId"]) {
        this._httpUserAuth.getClassDetailList(params["sessionId"], null, this, true);
      }
    });
    this._httpUserAuth.defaultCancellationPolicy(this, true)
  }
  upcommingclassupdate(event) {
    this.upcommingclassnumber = event + "";
  }
  whishlistclassupdate(event) {
    this.whishlistclassnumber = event + "";
  }

  onSuccess(type: any, responsedata: any) {
    if (type === Constants.CancellationPolicy) {
      LocalStorageService.setRefundPolicy(responsedata);
    }
    if (type == Constants.trainerFilterListApi) {

      if (responsedata.data.hasOwnProperty("toTime")) {
        this.joinclasspopup(responsedata.data);
      }
      else {
        console.error("Gym details error.");
      }
    }
  }

  isLoggedIn() {
    return UserSession.isLoggedIn();
  }

  joinclasspopup(classDetail: ClassDetail) {
    if (this.isLoggedIn()) {
      return this.modal.open(JoinclasspopupComponent, overlayConfigFactory({ classDetail }, BSModalContext));
    }
    else {
      return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));

    }
  }
  onFailure(type: any, response: string) {
  }

}


