import { MixpanelEvent } from './../../../../core/constants';
import { GlobalSubscriptionService } from './../../../../app/globalSubcription.service';
import { Component, OnInit } from '@angular/core';
import { DialogRef, ModalComponent, CloseGuard } from 'angular2-modal';
import { Modal,BSModalContext} from 'angular2-modal/plugins/bootstrap';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { HttpUserAuthUtilsService } from "../../../../core/http_wrapper/http-userauth-utils.service";
import { Constants } from "../../../../core/constants";
import { LocalStorageService } from '../../../../core/service/local-storage.service';
import { PlatformLocation } from '@angular/common'
import { ConfirmpopupComponent } from 'common/confirmpopup/confirmpopup.component';
import { MixpanelService } from '../../../../core/mixpanelService.service';
import { ClassDetail } from '../../../../core/models/classListResponse';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder,ReactiveFormsModule } from '@angular/forms';



declare var $: any;

export class AdditionPopupWindowData extends BSModalContext {
   public message: string;
   public title: string;
   public session: ClassDetail;
   public isTimeLessThanAllowedForRefund:any;




}

@Component({
  selector: 'app-userUnjoinPopup',
  templateUrl: './userUnjoinPopup.component.html',
  styleUrls: ['./userUnjoinPopup.component.css']
})

export class UserUnjoinConfirmPopup implements CloseGuard, ModalComponent<AdditionPopupWindowData>,OnInit {

  context: AdditionPopupWindowData;
  showLoader = false;
  message: string;
  title: string;
  session: ClassDetail;
  feedbackForm: FormGroup;
  cancellationId=undefined ;
  remark=undefined;
  cancellationReasonsData=[];
  isTimeLessThanAllowedForRefund;
  
  beforeDismiss(): boolean | Promise<boolean> {
    throw new Error('Method not implemented.');
  }
  beforeClose(): boolean | Promise<boolean> {
    throw new Error('Method not implemented.');
  }

  constructor(public location: PlatformLocation, public modal: Modal,
    public dialog: DialogRef<AdditionPopupWindowData>, private mixpanel: MixpanelService,
    private http_user: HttpUserAuthUtilsService, private globalSubscriptionService: GlobalSubscriptionService,private router: Router, public formBuilder: FormBuilder) {
  this.context = dialog.context;
  this.message = this.context.message;
  this.title = this.context.title;
  this.session = this.context.session;
  this.isTimeLessThanAllowedForRefund = this.context.isTimeLessThanAllowedForRefund;
  location.onPopState(() => {
  this.closePopup()
    });



   }

  ngOnInit() { 
    this.http_user.getCancellationReasons(this,false);
    this.initForm();
  }


  ngAfterViewInit(){
       $(".modal-dialog ").css("max-width", "400px");
    }

    initForm(){
      this.feedbackForm = this.formBuilder.group({
        reason : [''],
        comment : ['']
      });
    }

  closePopup(){
      this.dialog.close();
  }
    unjoinclass(){  
      this.remark =  this.feedbackForm.value.comment.trim().length > 0 ? this.feedbackForm.value.comment : undefined; 
    if(this.session.cancelType == 'FRIEND'){
      this.http_user.unjoinusersession(this.session.refBookingId,this.cancellationId ,this.remark, this, true);

    }else{
      this.http_user.unjoinusersession(this.session.joinClassId, this.cancellationId ,this.remark,this, true);

    }

  }
  openconfirmpopup(message, title) {
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title }, BSModalContext));
  }

  specialUnjoinPopup() { 
    var message = "The amount paid for this class was refunded to your account. Use your credit to book your next workout with us. Would you like to view your balance?";
    var title = "SUCCESS";
    var errorCode = "000";
    var okMessage = "YES";
    var parent = this;

    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title, errorCode,okMessage,parent }, BSModalContext));
   
  }

  timeCalculation(session: any, refundAllowedbefore: number, cancellationPolicy: any) {
    const sessionDate = new Date(session.classDate);
    const sessionTime = session.fromTime.split(':');
    sessionDate.setHours(sessionTime[0]);
    sessionDate.setMinutes(sessionTime[1]);
    sessionDate.setSeconds(sessionTime[2]);
    const today = new Date();
    let difference = Math.abs(sessionDate.getTime() - today.getTime());
    difference = (difference / 1000) / 3600;
    if (difference > refundAllowedbefore) {       
       this.specialUnjoinPopup();  
         }
  }

  onSuccess(type: any, responsedata: any) {
     if(type === Constants.unjoinUserClass) {
      this.closePopup();
      this.session.bookingTime = new Date();
      this.session.origin = 'UpComing';
      this.mixpanel.logEventWithJoinClass(MixpanelEvent.unjoin, this.session)
      LocalStorageService.setUser(responsedata);
      this.globalSubscriptionService.refreshUpcommingClasses('refresh')

      if(this.session.priceModel==='PT' || this.session.priceModel==='WORKSHOP') {
        // this.timeCalculation(this.session, +LocalStorageService.getRefundValue().refundAllowedbeforeForSpecial,
        // LocalStorageService.getRefundValue().specialClassCancellationPolicy)
    }

    }

    if(type === Constants.CancellationReasonsAPI){
      this.cancellationReasonsData = responsedata.data;
    }
  }
  onFailure(type: any, response: string) {
    this.closePopup();
    this.globalSubscriptionService.show("400", response);
    this.openconfirmpopup(response,'Does not Exist');
  }

  onReasonSelected(cancellationId){
    this.cancellationId = cancellationId;
  }

  confirmAction(){
        this.router.navigate(['/profile']);

  }
}



