import {UserRemoveWhishlistPopup} from '../userupcoming/removeWhishlistPopup/removeWhishlistPopup.component';
import {ClassDetail} from '../../../core/models/classListResponse';
import { MixpanelService } from './../../../core/mixpanelService.service';
import { Constants } from './../../../core/constants';
import { ConfirmpopupComponent } from './../../../common/confirmpopup/confirmpopup.component';
import { GlobalSubscriptionService } from './../../../app/globalSubcription.service';
import { LocalStorageService } from '../../../core/service/local-storage.service';
import { ModelpopupLrComponent } from '../../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { overlayConfigFactory, Modal } from 'angular2-modal';
import { JoinclasspopupComponent } from '../../../joinclasspopup/joinclasspopup.component';
import { UserSession } from './../../../core/sessions';
import { UserUpcomingClass } from '../../../core/models/userUpcomingClassesResponse';
import { HttpUserAuthUtilsService } from '../../../core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from './../../../core/http_wrapper/http_wrapper_response';
import { Component, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-userwishlist',
  templateUrl: './userwishlist.component.html',
  styleUrls: ['./userwishlist.component.css']
})
export class UserwishlistComponent implements OnInit, HttpSuccesFailureResponse, OnDestroy {
  whishList: ClassDetail[] = [];
  whisListCount = 0;
  private subscription: Subscription;
  isLoaded = false;
   @Output() updatewishListclasscount = new EventEmitter();
   currentPage = 1;
  isLoadMoreData = true;
  lastScrollDistance = 0;
  listIsLaoding = false;
  currency:any;
  public atGymText = UserSession.getDynamicElements(Constants.AtGymText)

  constructor(private globalSubscriptionService: GlobalSubscriptionService,
    private http_user: HttpUserAuthUtilsService,
    private modal: Modal) { }

  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();

        this.subscription = this.globalSubscriptionService.wishlistClassState
            .subscribe((wishlist) => {
              this.userwishlistapi();
               });
        this.userwishlistapi();
  }
  userwishlistapi() {
    this.resetBeforeLoad();
    this.listIsLaoding = true;
    this.http_user.userwhishListclasses(LocalStorageService.getUserId(), this.currentPage, this, true)
  }
  removeFromWishList(whishlist: ClassDetail) {
    const message = Constants.wishListAlert;
    const title = Constants.removeWishList;
    return this.modal.open(UserRemoveWhishlistPopup, overlayConfigFactory({message, title, whishlist}, BSModalContext));
    // this.http_user.removeFromWishList(whishList.wishListId,this,true);
  }
  onSuccess(type: any, responsedata: any) {
    if (type === Constants.UserWhishListClassesApi) {
      this.listIsLaoding = false;
    const temp_list: any[] = responsedata.data;
    temp_list.forEach(element => {
     this.whishList.push(element);
   });
  if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }
       this.whisListCount = this.whishList.length;
       this.updatewishListclasscount.next(this.whishList.length);

    }
      if (type === Constants.removeFromWishListApi) {
          //  this.openconfirmpopup("Class removed from wishlist","Success");

    }

  }
  onFailure(type: any, response: string) {
    this.listIsLaoding = false;
    if (type = Constants.UserWhishListClassesApi) {
    }
    if (type === Constants.removeFromWishListApi) {
    //  this.openconfirmpopup(response,"Error");
    }
  }
joinClass(classdetail) {
  classdetail.origin = 'WishList';
  if (LocalStorageService.getIsLoggedIn()) {
      return this.modal.open(JoinclasspopupComponent, overlayConfigFactory({classdetail}, BSModalContext));
    } else {
    return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));
    }
}
  ngOnDestroy() {
try{
  this.subscription.unsubscribe();
}
catch(e) {
//  console.log(e)
}
  }
      onScrollUp(event) {
    //     if(this.isLoadMoreData && this.lastScrollDistance<event.currentScrollPosition && !this.listIsLaoding){
    //       this.listIsLaoding=true;
    //       this.lastScrollDistance=event.currentScrollPosition;
    //       this.currentPage=this.currentPage+1;
    // this.http_user.userwhishListclasses(LocalStorageService.getUserId(),this.currentPage,this,false)
    //     }
      }
      resetBeforeLoad() {
        this.currentPage = 1;
        this.isLoadMoreData = true;
        this.whishList = [];

      }
  //     openconfirmpopup(message, title) {
  //   return this.modal.open(UserRemoveWhishlistPopup, overlayConfigFactory({message, title}, BSModalContext));
  // }
}



